.menu {
   position: relative;
   display: none;
   justify-content: center; 
   align-items: center;
   width: 48px;
   height: 48px;
   span {
      position: absolute;
      top: 15px;
      right: 8px;
      z-index: 4;
      width: 30px;
      height: 2px;
      background-color: $white;
      transition: .6s;
      &:nth-child(2) {
         top: 22px;
      }
      &:nth-child(3) {
         top: 29px;
      }
   }
   @media ( max-width: 1100px ) {
      display: flex;
   }
   &--active {
      span {
         transform: rotate(45deg);
         top: 22px;
         &:nth-child(2) {
            display: none;
         }
         &:nth-child(3) {
            transform: rotate(-45deg);
            top: 22px;
         }
      }
   }
}