.product {
   padding-top: 86px;
   padding-bottom: 135px;

   background-color: $white;
   @media ( max-width: 767px ) {
      padding-top: 40px;
      padding-bottom: 40px;
   }
   &__inner {
      display: flex;
      flex-direction: column;
   }
   &__title {
      font-family: $font-700;
      font-weight: 700;
      font-size: 3.3rem;
      color: $secondary-color;
      text-transform: uppercase;

      padding: 38px 0;
      padding-left: 100px;
      border-left: 1px solid $secondary-color;
      @media ( max-width: 576px ) {
         font-size: 2.2rem;
         line-height: 2.6rem;
         border-left: none;
         padding: 0;
      }
      span {
         color: $primary-color;
      }
   }
   &__nav {
      display: flex;
      align-items: center;

      margin-top: 45px;
      margin-bottom: 75px;
      @media ( max-width: 1200px ) {
         flex-wrap: wrap;
         margin-top: 35px;
         margin-bottom: 25px;
      }
      @media ( max-width: 767px ) {
         flex-direction: column;
         align-items: flex-start;
         margin-bottom: 10px;
      }
   }
   &__btn {
      font-family: $font-500;
      font-weight: 500;
      font-size: 1.3rem;
      color: #c5c5c5;
      text-transform: uppercase;

      border: solid 3px #f3f3f3;

      padding: 17px 50px;
      height: 55px;
      margin-right: 17px;

      cursor: pointer;
      transition: .2s;
      @media ( max-width: 1200px ) {
         width: calc( 50% - 17px);
         margin-bottom: 15px;
      }
      @media ( max-width: 767px ) {
         width: 100%;
         margin-right: 0;
         font-size: 1.2rem;
         white-space: nowrap;
      }
      &:hover {
         color: $primary-color;
         border: solid 3px $primary-color;
      }
      &--active {
         color: #7c97aa;
         background-color: #dce6ed;
         border: none;
         &:hover {
            color: #7c97aa;
            border: none;
         }
      }
      &:last-child {
         margin-right: 0;
      }
   }
   &__content {
      display: none;
      &--active {
         display: flex;
         justify-content: space-between;
      }
      @media ( max-width: 1200px ) {
         flex-wrap: wrap;
      }
   }
   &__text {
      display: flex;
      flex-direction: column;

      width: 100%;
      max-width: 313px;
      @media ( max-width: 1200px ) {
         max-width: 100%;
         margin-bottom: 60px;
      }
      @media ( max-width: 767px ) {
         margin-bottom: 20px;
      }
      span {
        // TODO UGLY привязка к тегу в редактируемой области, необходимо переписать!
         font-family: $font-300;
         font-weight: 300;
         font-size: 1.6rem;
         line-height: 2.4rem;
         color: $secondary-color;
         margin-bottom: 20px;
         max-width: 298px;
         @media ( max-width: 1200px ) {
            max-width: 100%;
         }
         @media ( max-width: 576px ) {
            font-size: 1.4rem;
            line-height: 1.8rem;
            margin-bottom: 15px;
         }
      }
   }
   &__link {
      font-family: $font-500;
      font-weight: 500;
      font-size: 1.3rem;
      line-height: 2rem;
      color: #f7f7f7;
      cursor: pointer;
      text-transform: uppercase;

      height: 114px;
      background-color: $secondary-color;
      box-shadow: 0px 15px 34px 0 rgba(12, 30, 44, 0.2);

      display: flex;
      align-items: center;

      padding-left: 50px;
      margin-top: 40px;

      transition: .2s;
      max-width: 314px;
      @media ( max-width: 1200px ) {
         margin-top: 0;
         width: 100%;
         max-width: 50%;
         height: 55px;
         br {
            display: none;
         }
      }
      @media ( max-width: 767px )  {
         font-size: 1.2rem;
         max-width: 100%;
      }
      @media ( max-width: 576px ) {
         padding-left: 20px;
         height: 48px;
         span {
            height: 48px;
            padding-left: 24px;
            padding-right: 21px;
         }
      }
      svg {
         width: 12px;
         height: 10px;
         fill: #f0d276;
         transition: .2s;
      }
      span {
         display: flex;
         justify-content: center;
         align-items: center;
         background-color: #153248;
         height: 114px;
         margin: 0;
         margin-left: 72px;
         padding-left: 34px;
         padding-right: 31px;
         transition: .2s;
         @media ( max-width: 1200px ) {
            margin-left: auto;
            height: 55px;
         }
         @media ( max-width: 767px )  {
            margin-bottom: 0;
         }
         @media ( max-width: 576px )  {
            margin-left: auto;
            margin-bottom: 0;
         }
      }
      &:hover {
         background-color: #153248;
         span {
            background-color: $secondary-color;
         }
         svg {
            fill:  #fcf7e6;
         }
      }
   }
   &__foto {
      img {
         border-radius: 15px;

         @media ( max-width: 1200px ) {
           max-height: 430px;
           object-fit: cover;
         }

         @include viewport--md {
           max-width: 360px;
         }

         @include viewport--sm {
           display: none;
         }
      }
   }

   &__wrap {
      display: flex;
      flex-direction: column;

      width: 100%;
      max-width: 272px;

      @media ( max-width: 1200px ) {
        width: 48%;
        max-width: 550px;
      }
      @include viewport--sm {
        width: auto;
        max-width: none;
      }
   }

   &__item {
      display: flex;
      flex-direction: column;

      margin-bottom: 31px;
      @media ( max-width: 950px )  {
         margin-bottom: 20px;
      }
      @media ( max-width: 576px ) {
         margin-bottom: 10px;
      }
      &:last-child {
         margin-bottom: 0;
      }
      span {
         font-family: $font-400;
         font-weight: 400;
         font-size: 1.4rem;
         color: #999;
         @media ( max-width: 576px ) {
            font-size: 1.2rem;
            line-height: 1.6rem;
         }
      }
   }
   &__subtitle {
      font-family: $font-700;
      font-weight: 700;
      font-size: 2rem;
      color: #34363b;
      margin-bottom: 10px;
      @media ( max-width: 576px ) {
         font-size: 1.6rem;
         line-height: 1.8rem;

         margin-bottom: 5px;
      }
   }
}
